.content {
  margin: 0 auto;
  min-height: 50vh;
  font-family: 'Inter';
}

@media screen and (max-width: 880px) {
  .content {
    margin: 0 19px;
  }
}

.content__section {
  margin: 0 auto;
  width: 100%;
  text-align: center;
  padding-top: 30px;
}

.content__section_gold {
  background: url('../../images/gold.webp') round;
}

.content__section_gold1 {
  background: url('../../images/goldd.jpeg') round;
}

.content__section_crypto {
  background: url('../../images/crypto.jpg') round;
}

.content__section_coins {
  background: url('../../images/coins.jpg') round;
}

.content__title {
  font-size: 20px;
  padding-top: 20px;
}

.content__title_clickable {
  cursor: pointer;
}

.content__text {
  margin: 0 10px;
}

.content__text_hidden {
  display: none;
}

.content__link {
  text-decoration: none;
  background-color: transparent;
  color: white;
}

